.container{
  background-color: chartreuse;
  max-width: 1172px
}
.headerbox {
  background-color: red;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 29px 0 0 0;
}
.lucidadventure{
  width: 70px;
  height: 18px;
  margin:auto 0;
}
.logobox{
  display: flex;
  justify-content: flex-start;
  background-color: lightblue;
  padding: 2px;
}
.logoimg{
  height: 16px;
  width: 44px;
  margin:auto 7px;
  padding: 2px 0 0 0;
}
.menubox{
  background-color: aqua;
  display: flex;
  flex-direction:column-reverse;
  ul{
    display: flex;
    flex-direction: row;
    font-size: 16px;
    line-height: 42px;
    font-weight: 600;
    letter-spacing: -0.07px;
    color: #fffefe;
    margin: -2px 0 0 1px;
}
  li{
    padding: 0 0 0 23.81px;

  }
}
